@import "../../common/utils/variables";


.header {

  background: #FCFCFC;

  --border-size: 3px;
  --border-angle: 0turn;
  background-image: conic-gradient(
                  from var(--border-angle),
                  #ffffff,
                  #ffffff 50%,
                  #ffffff
  ),
  conic-gradient(from var(--border-angle), transparent 20%, #08f, #f03);
  background-size: calc(100% - (var(--border-size) * 2)) calc(100% - (var(--border-size) * 2)),
  cover;
  background-position: center center;
  background-repeat: no-repeat;

  animation: bg-spin 5s linear infinite;
  @keyframes bg-spin {
    to {
      --border-angle: 1turn;
    }
  }

  &:hover {
    animation-play-state: paused;
  }


  &__wrapper {
    max-width: 1280px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    height: 60px;
  }

  &__logo {
    padding-top: 3px;
  }

  &__btn {
    background: $base-color;
    border: none;
    padding: 8px 28px;
    box-shadow: 0px 4px 18px rgba(54, 110, 255, 0.35), inset 0px 1px 0px rgba(255, 255, 255, 0.3);
    border-radius: 30px;
    color: #fff;
    cursor: pointer;
    text-decoration: none;

    transition: all .2s linear;

    &:hover {
      background: $base-color-hover;
    }
  }

  &__container {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 12px;
    cursor: pointer;
  }

  &__avatar {
    width: 36px;
    height: 36px;
  }

  &__name {

  }

  &__headerPopup {
    position: absolute;
    width: 112px;
    height: 104px;
    background: #FFFFFF;
    border: 1px solid #CFCFCF;
    box-shadow: 0px 2px 15px rgba(0, 0, 0, 0.06);
    right: -15px;
    top: 64px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    gap: 15px;
    padding-left: 20px;

    z-index: 1;
  }

  &__profile, &__logout {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 12px;
    cursor: pointer;
    text-decoration: none;
    color: #000000;
    transition: all .2s linear;

    &:hover {
      color: #e56200;
    }
  }
}

@property --border-angle {
  syntax: "<angle>";
  inherits: true;
  initial-value: 0turn;
}
